.footer{
    background-color: rgb(224, 227, 229);
    padding: 20px;
    height: auto;
}
.footer_wrapper{
    display: flex;
    justify-content: space-between;
    gap: 10px;
    flex-wrap: wrap;
}
.footer_content{
    width: 400px;
}
.logo{
    display: flex;
    gap: 5px;
}
.flogo{
    width: 40px;
    height: 40px;
}


@media (max-width: 1400px) {
    .footer_content{
        width: 350px;
    }
}

@media (max-width: 576px) {

    .footer_content h5{
        font-size: 100%;
    }
}