
.nav-bar {
    width: 100%;
    position: fixed;
    margin-top: -50px;
    z-index: 99;
    display: flex;
    justify-content: space-evenly;
    background-color: rgb(213, 223, 221);
    padding: 5px;
    
}

.logo,
img {
    /* width: 30%; */
    border-radius: 15%;
    height: 70px;
}

.menu {
    display: flex;
    gap: 40px;
}

.account {
    display: flex;
    gap: 20px;
}

/*                                                Hamburger */
.hamb{
    position: fixed;
    background-color: #D5DFDD;
    z-index: 99;
    width: 300px;
    top: 1px;
    margin-left: 110px;
    display: none;
}
.hamb-content{
    margin-top: 30px;
    margin-left: 20px;
}
.hamb_logo{
    display: none;
    position: fixed;
    z-index: 99;
    margin-top: -35px;
    margin-left: 290px;
}

/* Responsive */

@media (max-width:1000px) {
    .nav-bar {
        height: 80px;
    }
    .logo,img {
        margin-top: 6px;
        height: 50px;
    }
    .menu {
        margin-left: 30px;
        gap: 10px;
    }
    .account {
        margin-left: 30px;
        gap: 10px;
    }
}
@media (max-width:768px) {
    .nav-bar {
        height: 80px;
    }
    .logo,img {
        margin-top: 6px;
        height: 50px;
    }
    .menu {
        margin-left: 30px;
        gap: 10px;
    }
    .account {
        margin-left: 30px;
        gap: 10px;
    }
    .for{
        margin-top: 8px;
        height: 40px;
    }
}

@media (max-width:650px) {
    .nav-bar {
        justify-content: start;
    }

    .logo,img {
        margin-top: 8px;
        height: 40px;
    }

    .menu {
        display: none;
    }

    .account {
        display: none;
    }
    .hamb_logo{
        display: block;
    }
}
