
.cart{
    margin-top: 48px;
    padding-top: 80px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}
.cart_card{
    display: flex;
    justify-content: space-between;
    gap: 20px;
    padding: 10px;
    box-sizing: border-box;
}
.cart_orders{
    width: 900px;
}
.cart_total{
    width: 350px;
    height: 250px;
    padding: 20px;
    box-sizing: border-box;
}
.cart_image{
    width: 90px;
    height: 80px;
}

/*                                                     RESPONSIVE */

@media (max-width: 1400px) {
    .cart{
        padding-top: 70px;
    }
    h2{
        font-size: 250%;
    }
    .cart_image{
        width: 95px;
        height: 80px;
    }
    .cart_card h5{
        width: 120px;
    }
}

@media (max-width: 576px) {
    .cart{
        padding-top: 50px;
    }
    h2{
        font-size: 200%;
    }
    .cart_card{
        gap: 5px;
        justify-content: right;
        flex-wrap: wrap;
        padding: 5px;
    }
    .cart_orders{
        width: 340px;
    }
    .cart_image{
        width: 65px;
        height: 60px;
    }
    .cart_card h5{
        width: 120px;
    }
}