.gg {
    padding-top: 80px;
}

.home_banner {
    height: 34vw;
    /* margin-top: 110px; */
    /* margin: 110px auto 0px; */
    background: url('../../assets/images/food_banner2.jpg') no-repeat;
    background-size: contain;
    position: relative;
    border-radius: 2%;
}

.banner_content {
    text-align: center;
    position: relative;
    top: 10rem;
    font-size: 150%;
    box-sizing: border-box;
    color: black;
}

/*                                                 CATEGORY */

h2 {
    font-size: 200%;
}

.category {
    margin: 0px 150px;
    overflow: auto;
    padding: 10px 0px;
    /* scrollbar-color: transparent; */
    -webkit-overflow-scrolling: touch;
}

.category::-webkit-scrollbar {
    width: 10px;
}

.category::-webkit-scrollbar-thumb {
    background-color: rgb(203, 196, 196);
    /* color of the scroll thumb  */
    border-radius: 20px;
    /* roundness of the scroll thumb */
    /* border: 1px solid rgb(230, 20, 5); */
    /* creates padding around scroll thumb */
}

.category_wrapp {
    width: 150%;
    display: flex;
    gap: 30px;
}

.category_content {
    width: 200px;
    height: auto;
    font-size: 20px;
    overflow: hidden;
}

.cat_image {
    width: 170px;
    height: 160px;
    border-radius: 50%;
    border: 5px solid rgb(222, 219, 219);
    transition-duration: 0.2s;
}

.cat_image:hover {
    transform: scale(1.1);
}

/*                                                 Dishes */
.dishes {
    margin-left: 100px;
}

.dish_card {
    width: 270px;
    /* height: 450px; */
    border-radius: 10px;
    transition-duration: 0.2s;
}

.dish_card:hover {
    box-shadow: 1px 1px 10px black;
}

.dish_image {
    width: 100%;
    height: 250px;
}

.dish_card_body {
    padding: 0px 10px;
    box-sizing: border-box;
}

.dish_wrapper {
    display: flex;
    gap: 10px;
    /* width: 180%; */
    flex-wrap: wrap;
}


/*                                                RESPONSIVE */
@media (max-width: 1400px) {

    .banner_content {
        top: 4rem;
        font-size: 20px;
    }

    h2 {
        font-size: 130%;
    }
    .dishes {
        margin-left: 30px;
    }

    .category {
        margin-left: 10px;
        width: 98%;
    }

    .category_wrapp {
        width: 150%;
    }
    .category_content {
        width: 150px;
        /* height: auto; */
        font-size: 140%;
    }

    .cat_image {
        width: 120px;
        height: 110px;
        border: 5px solid rgb(222, 219, 219);
    }
    .dish_card {
        width: 205px;
        font-size: 80%;
    }

    h5 {
        font-size: 150%;
    }

    .dish_image {
        width: 100%;
        height: 150px;
    }

    .dish_card_body {
        padding: 0px 5px;
        box-sizing: border-box;
    }
}

/* @media (max-width: 992px) {
