} */


@media (max-width: 576px) {
    .home_banner {
        margin-left: 10px;
    }

    .banner_content {
        top: 1.5rem;
        font-size: 10px;
        padding: 0px 20px;
    }

    .banner_content p {
        font-size: 0%;
    }

    h2 {
        font-size: 80%;
    }

    .category {
        margin-left: 10px;
        width: 340px;
    }

    .category_wrapp {
        width: 220%;
        gap: 10px;
    }

    .category_content {
        width: 110px;
        /* height: 170px; */
        font-size: 100%;
    }

    .cat_image {
        width: 100px;
        height: 90px;
        border: 5px solid rgb(222, 219, 219);
    }

    .dishes {
        margin-left: 10px;
    }

    .dish_card {
        width: 150px;
        font-size: 80%;
    }

    h5 {
        font-size: 150%;
    }

    .dish_image {
        width: 100%;
        height: 100px;
    }

    .dish_card_body {
        padding: 0px 5px;
        box-sizing: border-box;
    }

    .dish_wrapper {
        gap: 10px;
    }
}